import { ACTION_EDIT } from '../../request';
import { Voter } from '../voter';

export class PageStylingVoter extends Voter {
    /**
     * @param {ACLVoter} acl ACL voter instance
     */
    constructor(acl) {
        super();
        this.acl = acl;
    }

    supports(request) {
        return request.resource === 'page-styling';
    }

    vote(request) {
        const grant = this.acl.vote(request);

        if (grant && request.includes(ACTION_EDIT) && !this.canEdit(request)) {
            return false;
        }

        //
        return grant;
    }

    /**
     * @param {AccessPermissionRequest} request
     * @returns {boolean}
     */
    canEdit(request) {
        return request.subject ? this.acl.grantsInArrayPayload(request, request.subject.type) : true;
    }
}
