import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { IoMdImage } from 'react-icons/io';

import { __t, mediaUtils } from '@mssgme/shared';
import { LazyImage } from '@mssgme/ui';

import styles from './styles.scss';

export const ProductItem = ({ tag: Tag, product: { name, price, published, photoUrl }, themeStyles, ...rest }) => {
    return (
        <Tag className={classNames(styles.root, !published && styles.disabled)} {...rest}>
            <div className={styles.thumbnail} style={{ backgroundColor: themeStyles.backgroundColor }}>
                {photoUrl ? (
                    <LazyImage src={mediaUtils.generateProductThumbnailUrl(photoUrl)} alt={name} />
                ) : (
                    <div className={styles.emptyPlaceholder}>
                        <IoMdImage size="40%" color="#fff" />
                    </div>
                )}
            </div>
            <div className={styles.meta} style={{ color: themeStyles.color }}>
                <span className={styles.title}>{name}</span>
                {price && (
                    <span className={styles.price}>
                        {price.formattedDiscount &&
                            (price.discount === 0 ? (
                                <span className={styles.discountPrice}>{__t('free')}</span>
                            ) : (
                                <span className={styles.discountPrice}>{price.formattedDiscount}</span>
                            ))}
                        {price.amount === 0 ? (
                            __t('free')
                        ) : (
                            <span className={price.formattedDiscount ? styles.lineThrough : null}>
                                {price.formatted}
                            </span>
                        )}
                    </span>
                )}
            </div>
        </Tag>
    );
};

ProductItem.propTypes = {
    product: PropTypes.object.isRequired,
    themeStyles: PropTypes.object,
    tag: PropTypes.any,
};

ProductItem.defaultProps = {
    tag: 'div',
    themeStyles: {},
};
