import { parse } from '../request';
import { Resource } from './resource';
import { Role } from './role';
import { ACL } from './acl';

export class ACLLoader {
    constructor(definitions) {
        this.definitions = definitions;
    }

    load() {
        const acl = new ACL();

        for (const {
            id,
            rules: { extend, grant, reject },
        } of this.definitions) {
            const parent = extend && acl.getRole(extend);
            const role = acl.addRole(new Role(id, parent));

            for (const [rules, granted] of [[grant, true], [reject, false]]) {
                if (rules) {
                    for (const rule of rules) {
                        role.addResource(this.parseRuleDefinition(rule, granted));
                    }
                }
            }
        }

        return acl;
    }

    parseRuleDefinition(value, granted) {
        if (typeof value === 'object') {
            const { rule, ...payload } = value;
            const parsed = this.parseRuleDefinition(rule, granted);

            parsed.payload = payload;

            return parsed;
        }

        const { resource, actions } = parse(value);

        return new Resource(resource, actions.reduce((acc, action) => ((acc[action] = granted), acc), {}));
    }
}
