import { ACTION_CREATE, ACTION_EDIT, ACTION_DELETE } from '../../request';
import { Voter } from '../voter';

export class PageTemplatesVoter extends Voter {
    supports(request) {
        return request.resource === 'page-templates';
    }

    vote(request) {
        if (request.includes(ACTION_CREATE)) {
            if (!this.canCreate(request)) {
                return false;
            }
        }

        if (request.includes(ACTION_EDIT)) {
            if (!this.canEdit(request)) {
                return false;
            }
        }

        if (request.includes(ACTION_DELETE)) {
            if (!this.canDelete(request)) {
                return false;
            }
        }

        return true;
    }

    /**
     * @param {AccessPermissionRequest} request
     * @returns {boolean}
     */
    canCreate(request) {
        const count = request.user.workspace.quantities.templates;

        switch (request.role) {
            case 'unlimited':
                return count < 20;

            default:
                return false;
        }
    }

    /**
     * @param {AccessPermissionRequest} request
     * @returns {boolean}
     */
    canDelete(request) {
        return this.canEdit(request);
    }

    /**
     * @param {AccessPermissionRequest} request
     * @returns {boolean}
     */
    canEdit(request) {
        if (!request.subject?._id) {
            return true;
        }

        return String(request.subject.author) === request.currentUserId;
    }
}
