import React, { useMemo } from 'react';
import { Form, Formik } from 'formik';

import { __t } from '@mssgme/shared';
import { Textarea } from '@mssgme/ui';
import { useCRMData, useSavedOrders } from '../../../hooks';
import { TextField } from '../../../UI';
import { useOrderSubmit } from '../../useOrderSubmit';
import { StepsFooter } from './StepsFooter';

const INITIAL_CONTACT_INFO = {
    firstName: '',
    lastName: '',
    phoneNumber: '',
    comment: '',
};

function ContactInfoStep() {
    const { form } = useCRMData();
    const { currentOrder } = useSavedOrders();
    const initialValues = useMemo(() => ({ contactInfo: currentOrder?.contactInfo || INITIAL_CONTACT_INFO }), [
        currentOrder?.contactInfo,
    ]);
    const doSubmit = useOrderSubmit(currentOrder?._id);
    const noLastName = !!form.optional?.lastName;

    if (!currentOrder) {
        return null;
    }

    return (
        <Formik initialValues={initialValues} onSubmit={doSubmit}>
            {({ isSubmitting, handleSubmit }) => (
                <Form>
                    <TextField
                        required
                        name="contactInfo.firstName"
                        label={noLastName ? __t('contactInfo.name') : __t('contactInfo.firstName')}
                        placeholder={
                            noLastName ? __t('contactInfo.placeholder.name') : __t('contactInfo.placeholder.firstName')
                        }
                    />
                    {!noLastName && (
                        <TextField
                            required
                            name="contactInfo.lastName"
                            placeholder={__t('contactInfo.placeholder.lastName')}
                        />
                    )}
                    <TextField
                        required
                        name="contactInfo.phoneNumber"
                        placeholder={__t('contactInfo.placeholder.phoneNumber')}
                    />
                    <TextField
                        component={Textarea}
                        name="contactInfo.comment"
                        placeholder={__t('contactInfo.placeholder.comment')}
                    />

                    <StepsFooter prev next={handleSubmit} disabled={isSubmitting} />
                </Form>
            )}
        </Formik>
    );
}

export default ContactInfoStep;
